@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap);


.gol-grid {
    position: fixed;
    z-index: -10;
    width: 100%;
    height: 100vh;
}

.gol-row {
    display: flex;
}

.gol-cell {
    --size: 15px;
    width: var(--size);
    height: var(--size);
    transition: background-color 0.8s ease-in-out;
}

.alive {
    background-color: hsla(var(--gol-alive));
}
:root {

    --primary-base: 20;
    --gol-alpha: 0.05;
    --projects-alpha: 0.5;
    --shadow-alpha: 0.3;

    /* Light Mode */
    --bg-light: 0, 0%, 96%;
    --text-light: 0, 0%, 0%;
    --primary-1-light: var(--primary-base), 100%, 90%;
    --primary-2-light: var(--primary-base), 46%, 85%;
    --primary-3-light: var(--primary-base), 40%, 92%;
    --gol-alive-light: 0, 0%, 0%, var(--gol-alpha);
    --cv-button-light: var(--primary-base), 100%, 65%;
    --cv-button-hover-light: var(--primary-base), 100%, 75%;
    --projects-light: 130, 20%, 78%, var(--projects-alpha);
    --shadow-light: 0, 10%, 66%, var(--shadow-alpha);
    --description-light: 215, 95%, 88%;

    /* Dark Mode */
    --bg-dark: 245, 17%, 21%;
    --text-dark: 0, 0%, 100%;
    --primary-1-dark: var(--primary-base), 12%, 33%;
    --primary-2-dark: var(--primary-base), 5%, 28%;
    --primary-3-dark: var(--primary-base), 12%, 50%;
    --gol-alive-dark: 245, 100%, 100%, var(--gol-alpha);
    --cv-button-dark: var(--primary-base), 8%, 50%;
    --cv-button-hover-dark: var(--primary-base), 8%, 70%;
    --projects-dark: 130, 14%, 33%, var(--projects-alpha);
    --shadow-dark: 0, 33%, 73%, var(--shadow-alpha);
    --description-dark: 215, 27%, 40%;
}

:root[color-scheme="dark"] {

    --bg: var(--bg-dark);
    --text: var(--text-dark);
    --primary-1: var(--primary-1-dark);
    --primary-2: var(--primary-2-dark);
    --primary-3: var(--primary-3-dark);
    --gol-alive: var(--gol-alive-dark);
    --cv-button: var(--cv-button-dark);
    --cv-button-hover: var(--cv-button-hover-dark);
    --projects: var(--projects-dark);
    --shadow: var(--shadow-dark);
    --description: var(--description-dark);
    color-scheme: dark;
}

:root[color-scheme="light"] {

    --bg: var(--bg-light);
    --text: var(--text-light);
    --primary-1: var(--primary-1-light);
    --primary-2: var(--primary-2-light);
    --primary-3: var(--primary-3-light);
    --gol-grid: var(--gol-grid-light);
    --gol-alive: var(--gol-alive-light);
    --cv-button: var(--cv-button-light);
    --cv-button-hover: var(--cv-button-hover-light);
    --projects: var(--projects-light);
    --shadow: var(--shadow-light);
    --description: var(--description-light);
    color-scheme: light;
}
.projects-container {
    display: flex;
    width: 90%;
    margin: 0 auto;
}

.projects-container ul {
    width: 37.5%;
    margin-right: 2.5%;
    padding-left: 0;
    padding-right: 0;
}

.projects-container .side {
    width: 60%;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    position: relative;
}

.projects-container .side img {
    width: 100%;
    box-shadow: 7px 7px 10px hsla(var(--shadow));
    border-radius: 15px;
    -webkit-user-select: none;
            user-select: none;
}

.projects-container li {
    display: flex;
    align-items: center;
    background-color: hsla(var(--projects));
    height: 55px;
    margin: 0 20px 15px 0;
    padding: 0 10px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    /* transition: background-color 0.2s ease-in-out; */
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    transition: scale 0.2s ease-in-out;
    box-shadow: 5px 5px 5px hsla(var(--shadow));
}

.projects-container li:hover {
    scale: 1.03;
}

.text-balloon {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 16px;
    text-align: center;
    position: absolute;
    top: 10px;
    left: 50%;
    background-color: hsl(var(--description));
    padding: 10px;
    border-radius: 15px;
    width: 350px;
    box-shadow: 0 2px 5px hsla(var(--shadow));
}
  
.triangle {
    position: absolute;
    bottom: -15px;
    left: calc(50% - 15px);
    width: 0;
    height: 0;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-top: 15px solid hsl(var(--description));
    -webkit-filter: drop-shadow(0px 2px 2px hsla(var(--shadow)));
            filter: drop-shadow(0px 2px 2px hsla(var(--shadow)));
}
@media (max-width: 1700px) {

    .projects-container {

        width: 1050px;
    }

    .projects-container ul {
        margin: 0;
        width: 30%;
    }

    .projects-container .side {
        width: 70%;
    }

    .side a {
        height: 90%;
    }

    .side img {
        height: 100%;
    }

    .text-balloon {
        left: 75%;
    }
}
html, body, #root, .App {
    height: 100%;
}

a {
    color: hsl(var(--text));
    text-decoration: none;
}

body {
    margin: 0;
    background-color: hsl(var(--bg));
    color: hsl(var(--text));
    /* transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out; */
    font-family: 'Manrope', sans-serif;
}

.theme-switch {
    float: right;
    padding: 15px 20px 15px 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    width: 120px;
}

.float-break {
    clear: both;
}

.cv-outline {
    border: 1px solid hsl(var(--cv-button));
    position: fixed;
    width: 130px;
    height: 130px;
    border-radius: 50%;
    left: 20px;
    top: 20px;
    padding: 10px;
    -webkit-animation: padding-jiggle 2s infinite;
            animation: padding-jiggle 2s infinite;
}

.cv-button {
    background-color: hsl(var(--cv-button));
    width: 100%;
    height: 100%;
    border-radius: 50%;
    text-align: center;
    display: flex;
    align-items: center;
    /* transition: background-color 0.2s ease-in-out; */
    font-size: 15px;
}

.cv-button h1 {
    /* transition: color 0.2s ease-in-out; */
}

.cv-button:hover {
    background-color: hsl(var(--cv-button-hover));
}

.logo-container {
    position: fixed;
    top: 20px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
            user-select: none;
}

@-webkit-keyframes padding-jiggle {
    0% {
        padding: 10px;
    }
    50% {
        padding: 0;
    }
    100% {
        padding: 10px;
    }
}

@keyframes padding-jiggle {
    0% {
        padding: 10px;
    }
    50% {
        padding: 0;
    }
    100% {
        padding: 10px;
    }
}

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 90%;
}

.main-component {
    position: absolute;
}

.home-page {
    margin-bottom: 5%;
}

.presentation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.presentation h1 {
    font-size: 50px;
    font-family: 'Outfit', sans-serif;
    text-align: center;
}

.presentation p {
    font-size: 25px;
    max-width: 50%;
}

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-component {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    position: fixed;
    bottom: 0;
    margin-bottom: 50px;
}

.contact-component .links {
    height: 200px;
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.link-container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 10px;
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px);
    transition: opacity 0.4s ease-in-out, scale 0.2s ease-in-out, -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out, scale 0.2s ease-in-out;
    transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out, scale 0.2s ease-in-out, -webkit-transform 0.4s ease-in-out;
    -webkit-user-select: none;
            user-select: none;
    cursor: pointer;
    z-index: 100;
}

.link-container.above {
    -webkit-transform: translate(0, 25px);
            transform: translate(0, 25px);
}

.link-container:hover {
    scale: 1.2 1.2;
}

.contact-displayer {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 100;
}

.contact-displayer p {
    transition: opacity 0.4s ease-in-out;
}

.invisible {
    opacity: 0;
    pointer-events: none;
}

.down-button {
    border: 2px solid hsl(var(--text));
    border-radius: 50%;
    padding: 10px 15px 20px 15px;
    margin: 0;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: scale 0.2s ease-in-out, border 0.2s ease-in-out;
    -webkit-animation: jiggle 2s infinite;
            animation: jiggle 2s infinite;
}

.down-button:hover, .side-button:hover {
    scale: 1.1 1.1;
}

.arrow {
    width: 20px;
	height: 20px;
    border-width: 3px 3px 0 0;
    border-style: solid;
    border-color: var(--text);
    -webkit-transform: translate(0, 50%) rotate(315deg);
            transform: translate(0, 50%) rotate(315deg);
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.arrow.down {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

@-webkit-keyframes jiggle {
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    50% {
        -webkit-transform: translate(0, -10px);
                transform: translate(0, -10px);
    }
    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

@keyframes jiggle {
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
    50% {
        -webkit-transform: translate(0, -10px);
                transform: translate(0, -10px);
    }
    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

.copyright {
    position: fixed;
    bottom: 0;
    margin-bottom: 10px;
}

.nav-button-area {
    position: fixed;
    top: 50%;
    display: flex;
    align-items: center;
    margin: 0 20px;
}

.nav-button-area.right-button {
    right: 0;
}

.nav-button-area.left-button {
    flex-direction: row-reverse;
}

.nav-button-area p {
    margin: 0 10px;
}

.side-button {
    border: 2px solid hsl(var(--text));
    border-radius: 50%;
    padding: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    transition: scale 0.2s ease-in-out, border 0.2s ease-in-out;
}

.right-button .side-button {
    -webkit-animation: right-jiggle 2s infinite;
            animation: right-jiggle 2s infinite;
}

.left-button .side-button {
    -webkit-animation: left-jiggle 2s infinite;
            animation: left-jiggle 2s infinite;
}

.left-button svg {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

@-webkit-keyframes right-jiggle {
    0% {
        -webkit-transform: translate(5px ,0);
                transform: translate(5px ,0);
    }
    50% {
        -webkit-transform: translate(-5px ,0);
                transform: translate(-5px ,0);
    }
    100% {
        -webkit-transform: translate(5px ,0);
                transform: translate(5px ,0);
    }
}

@keyframes right-jiggle {
    0% {
        -webkit-transform: translate(5px ,0);
                transform: translate(5px ,0);
    }
    50% {
        -webkit-transform: translate(-5px ,0);
                transform: translate(-5px ,0);
    }
    100% {
        -webkit-transform: translate(5px ,0);
                transform: translate(5px ,0);
    }
}

@-webkit-keyframes left-jiggle {
    0% {
        -webkit-transform: translate(-5px ,0);
                transform: translate(-5px ,0);
    }
    50% {
        -webkit-transform: translate(5px ,0);
                transform: translate(5px ,0);
    }
    100% {
        -webkit-transform: translate(-5px ,0);
                transform: translate(-5px ,0);
    }
}

@keyframes left-jiggle {
    0% {
        -webkit-transform: translate(-5px ,0);
                transform: translate(-5px ,0);
    }
    50% {
        -webkit-transform: translate(5px ,0);
                transform: translate(5px ,0);
    }
    100% {
        -webkit-transform: translate(-5px ,0);
                transform: translate(-5px ,0);
    }
}
@media (max-width: 900px) {
    
    .nav-button-area {
        width: 115px;
        margin: 0;
    }

    .nav-button-area, .nav-button-area.left-button {
        flex-direction: column-reverse;
        align-items: center;
    }

    .nav-button-area p {
        margin: 10px;
    }

    .presentation {
        height: 400px;
        justify-content: start;
    }

    .presentation h1 {
        font-size: 40px;
        margin-bottom: 110px;    
    }
    
    .presentation p {
        font-size: 20px;
        max-width: none;
        width: 70%;
        text-align: center;
    }
}

@media (max-width: 693px) {

    .presentation h1 {
        margin-bottom: 60px;    
    }

    .presentation p {
        font-size: 18px;
    }

    .nav-button-area {
        width: 100px;
    }

    .nav-button-area p, .contact-displayer p {
        font-size: 14px;
    }

    .side-button {
        padding: 5px;
    }

    .right-button .side-button svg {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }

    .left-button .side-button svg {
        -webkit-transform: scale(0.8) rotate(180deg);
                transform: scale(0.8) rotate(180deg);
    }
}

@media (max-width: 600px) {

    .presentation p {
        width: 60%;
    }
}

@media (max-width: 470px) {

    .presentation h1 {
        font-size: 30px;
        margin-bottom: 80px;
    }

    .presentation p {
        font-size: 15px;
        width: 50%;
    }

    .cv-outline {
        width: 100px;
        height: 100px;
    }

    .cv-button {
        font-size: 12px;
    }

    .logo {
        width: 75px;
    }
}

@media (max-height: 800px) {

    .contact-component {
        margin-bottom: 30px;
    }
}
